// Navigation breakpoints
.desktop-menu, .top-bar-left {
  @include show-for(medium);
}

#offCanvas, .title-bar {
  position: absolute;
  width: 100%;
  padding:0;
  @include hide-for(medium);
}

// Mobile menu
.off-canvas {
  > ul.menu {
    height: 100vh;
    padding: 1rem;

    a {
      color: $black;
      font-weight: 600;
      font-size: rem-calc(18);
      opacity: .75;
    }

    .is-accordion-submenu-parent > a::after {
      display: block;
      width: 0;
      height: 0;
      border: inset 6px;
      content: '';
      border-bottom-width: 0;
      border-top-style: solid;
      border-color: $white transparent transparent;
      position: absolute;
      top: 50%;
      margin-top: -3px;
      right: 1rem;
      }
  }

  .menu .active > a { background-color: lighten($outer-space, 10%); }


}

.title-bar-title {

  a {
    font-size: 1rem;
    color: #B9B9B9;
  }
}

// Tablet and desktop menu
.top-bar {
  height: 100px;
  background-color: #FFF;
  .top-bar-left .menu, .top-bar-right .menu {

    height: 100px;
    background-color: #FFF;
    color: black;
    border: none;
  }
  .top-bar-right, .top-bar-left {
    display: flex;
    align-items: center;
    // justify-content: center;
    .menu-item {
      height: 100px;
    }
  }
  .menu .home {
      height: 100px;
    a {
    font-size: 1rem;
    background-color: #FFF;
    color: black;
  }
}

  .menu a {
    height: 45px;
    line-height: 45px;
    color: black;
    padding-top: 0;
    padding-bottom: 0;
    font-weight: bold;
    font-size: 0.8rem;
    line-height: 1;

    &:hover:not(.button) {
      background-color: #42525D;
    }
  }

  .menu .active > a { background-color: lighten($outer-space, 10%); }

  .menu>li:not(.menu-text)>a {
    padding: none; }
  .menu li:not(:last-child) { border-right: none; }

  .dropdown.menu .submenu { border: 0; }
  .dropdown.menu .has-submenu.is-down-arrow a { padding-right: 1rem; }
  .dropdown.menu .has-submenu.is-down-arrow > a::after { border: 0; }
  .dropdown.menu:first-child > li.is-dropdown-submenu-parent > a::after { display: none; }
}

// Basic styles for post navigation. Nicolas Gallagher's "Micro clearfix"
// https://css-tricks.com/snippets/css/clear-fix/
.post-navigation:before,
.post-navigation:after {
  content: "";
  display: table;
}

.post-navigation:after {
  clear:both;
}

.nav-previous { float:left; }
.nav-next { float:right; }
