/* --- EASYDROPDOWN DEFAULT THEME --- */
/* PREFIXED CSS */
.easy-dropdown,
.easy-dropdown div,
.easy-dropdown div::after,
.easy-dropdown li {
  transition: all 0.3s ease-in-out; }

.easy-dropdown .selected::after,
.easy-dropdown.scrollable div::after {
  pointer-events: none; }

.selected::after {
  background: transparent; }

/* WRAPPER */
.easy-dropdown {
  position: relative;
  width: 160px;
  border: none;
  cursor: pointer;
  background: transparent;
  border-radius: 3px;
  user-select: none; }
  @media screen and (max-width: 89.9375em) {
    .easy-dropdown {
      width: 114px; } }

.easy-dropdown.open {
  z-index: 2; }

/* CARAT */
.easy-dropdown .carat {
  position: absolute;
  right: 12px;
  top: 50%;
  margin-top: -4px; }

.easy-dropdown.open .carat {
  margin-top: -10px;
  border-top: 6px solid transparent;
  border-bottom: 8px solid #000; }

.easy-dropdown.disabled .carat {
  border-top-color: #999; }

/* OLD SELECT (HIDDEN) */
.easy-dropdown .old {
  position: absolute;
  left: 0;
  top: 0;
  height: 0;
  width: 0;
  overflow: hidden; }

.easy-dropdown select {
  position: absolute;
  left: 0;
  top: 0; }

.easy-dropdown.touch .old {
  width: 100%;
  height: 100%; }

.easy-dropdown.touch select {
  width: 100%;
  height: 100%;
  opacity: 0; }

/* SELECTED FEEDBACK ITEM */
.easy-dropdown .selected,
.easy-dropdown li {
  display: block;
  font-size: 18px;
  line-height: 1;
  font-family: "Open Sans", sans-serif;
  font-size: 1em;
  font-weight: 600;
  color: #2A2B2A;
  transition: color, text-decoration 0.3s ease-in-out;
  text-transform: uppercase;
  color: #2A2B2A;
  overflow: hidden;
  white-space: nowrap; }
  .easy-dropdown .selected:active, .easy-dropdown .selected:hover,
  .easy-dropdown li:active,
  .easy-dropdown li:hover {
    color: #56B881; }
  @media screen and (max-width: 74.9375em) {
    .easy-dropdown .selected,
    .easy-dropdown li {
      font-size: 0.8em; } }
  @media screen and (max-width: 63.9375em) {
    .easy-dropdown .selected,
    .easy-dropdown li {
      font-size: 0.6em; } }

.easy-dropdown.disabled .selected {
  color: #999; }

.easy-dropdown .selected::after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 60px;
