.burger {
	cursor: pointer;
	float: right;
	box-sizing: border-box;
	position: fixed;
	top: 22px;
	right: 22px;
	width: 20px;
	height: 16px;
	z-index: 999;

	span {
		width: 100%;
		height: 1px;
		border-radius: 1px;
		background-color: #000;
		transform: rotate(0deg);
		transition: all 0.3s ease-in-out;

		&:nth-child(1) {
			position: absolute;
			top: 0;
			left: 0;
		}

		&:nth-child(2) {
			position: absolute;
			visibility: visible;
			top: 50%;
			margin-top: 0;
			left: 0;
			transition: all 0.1s ease-in-out;
		}

		&:nth-child(3) {
			position: absolute;
			top: 100%;
			left: 0;
		}
	}
}

.thex {
	span {
		&:nth-child(1) {
			left: 0;
			transform: rotate(135deg);
			top: 50%;
			margin-top: -2px;

		}

		&:nth-child(2) {
			visibility: hidden;
			height: 0;
			top: 50%;
			margin-top: 0;
			left: 0;

		}

		&:nth-child(3) {
			left: 0;
			transform: rotate(45deg);
			top: 50%;
			margin-top: -2px;
		}
	}
}

.stop-scrolling {
	height: 100%;
	overflow: hidden;
}
